.intro-mask {
  width: 100vw;
  height: 100vh;
  z-index: 15;
  opacity: 1;
  background-color: #1f1b3b;
  position: absolute;
}

.loading-wrapper {
  flex-direction: column;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loading-wrapper p {
  margin: 0 auto;
  font-size: 1.8rem;
  line-height: 1;
}

.lds-grid {
  width: 90px;
  height: 90px;
}

.lds-grid div {
  width: 20px;
  height: 20px;
  background: #1ef1bf;
  border-radius: 0%;
  animation: lds-grid 1.2s linear infinite;
  position: absolute;
}

.lds-grid div:nth-child(1) {
  animation-delay: 0s;
  top: 8px;
  left: 8px;
}

.lds-grid div:nth-child(2) {
  animation-delay: -.4s;
  top: 8px;
  left: 32px;
}

.lds-grid div:nth-child(3) {
  animation-delay: -.8s;
  top: 8px;
  left: 56px;
}

.lds-grid div:nth-child(4) {
  animation-delay: -.4s;
  top: 32px;
  left: 8px;
}

.lds-grid div:nth-child(5) {
  animation-delay: -.8s;
  top: 32px;
  left: 32px;
}

.lds-grid div:nth-child(6) {
  animation-delay: -1.2s;
  top: 32px;
  left: 56px;
}

.lds-grid div:nth-child(7) {
  animation-delay: -.8s;
  top: 56px;
  left: 8px;
}

.lds-grid div:nth-child(8) {
  animation-delay: -1.2s;
  top: 56px;
  left: 32px;
}

.lds-grid div:nth-child(9) {
  animation-delay: -1.6s;
  top: 56px;
  left: 56px;
}

@keyframes lds-grid {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: .4;
  }
}

.navbar {
  width: 100vw;
  z-index: 10;
  padding: 6rem 3rem 3rem 6rem;
  transition: all .5s ease-in-out;
  position: fixed;
}

.navbar .nav-contents {
  font-family: Nunito, sans-serif;
  display: flex;
}

.navbar .nav-contents .nav-header {
  font-family: Iceberg, cursive;
  font-size: 2.4rem;
}

.navbar .nav-contents .nav-header a {
  flex-direction: row;
