  padding: 0 44px;
}

.navbar .nav-contents .cta-btn {
  background-color: inherit;
  color: #1ef1bf;
  width: 160px;
  border: 2px solid #1ef1bf;
  border-radius: 8px;
  font-size: 2rem;
  font-weight: bold;
}

.navbar .nav-contents .cta-btn a {
  color: #ecebf2;
  text-decoration: none;
}

.navbar .nav-contents button:hover {
  transition: all .3s;
  box-shadow: 0 0 14px #1ef1bf;
}

.navbar .nav-contents button:hover a {
  color: #1ef1bf;
  cursor: pointer;
}

.navbar-initial {
  background: linear-gradient(#070a11 0%, #0000 100%);
}

.navbar-alt {
  -webkit-backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  backdrop-filter: blur(12px);
  background-color: #1f1b3bb3;
  padding-top: 3rem;
}

.navbar-alt .nav-toggle {
