    visibility: hidden;
    -webkit-box-align: left;
    -ms-flex-align: left;
    align-items: left;
  }

  .nav-contents .nav-header {
    display: none;
  }

  .nav-contents .nav-links {
    visibility: hidden;
    color: #1f1b3b;
    -webkit-box-align: left;
    -ms-flex-align: left;
    align-items: left;
    flex-direction: column;
    margin: 0;
